import { urlApp, langParamApp } from './apps';
import { getAllLocales, getAppNameFromURL } from './utils';
import getAppLocale from './getAppLocale';

/*
 * @param {string} url - the destination app url
 * @param {string} locale - locale you want to pass, it doesn not have to be the one destination app uses.
 * @returns {string} destination app localized url
 */
const getLocalizedURL = (url, locale) => {
  
  try {
    if (typeof url !== 'string' || typeof locale !== 'string') {
      throw Error('expect "url" and "locale" to be string.');
    }
    
    url = url.trim()

    if (url.toLowerCase().includes('adobe.com/go/')) {
      return url;
    }
    
    let localizedUrl = url;
    const app = getAppNameFromURL(url);
    
    if (app === undefined) {
      throw Error(`this app is not supported yet: "${url}"`);
    }

    let appLocale = getAppLocale(app, locale);

    if (appLocale) {
      const allLocales = getAllLocales();

      if (urlApp.includes(app)) {
        
        // remove english path as it doesn't work for adobe.com and helpx URL w/paths
        if (appLocale === 'en' || appLocale === 'us' || appLocale === 'en-US') {
          appLocale = '';
        }

        let pattern = (app === 'acrobat_plan') ? /adobe\.com\/(.{5,10}?)(?=\/|$)/ : /adobe\.com\/(.*?)(?=\/|$)/;
        const thisLocale = (url.match(pattern) || [])[1];

        if (thisLocale !== undefined && allLocales[thisLocale] !== undefined) {
          localizedUrl = url.replace(`.com/${thisLocale}`, appLocale === '' ? '.com' : `.com/${appLocale}`);
        } else {
          if (appLocale !== '') {
            localizedUrl = url.replace('.com', `.com/${appLocale}`);
          }

          // [ISENG-2617]: support HelpX User Guide URLs
          if (app === 'helpx' && url.includes('.ug.html')) {
            localizedUrl = localizedUrl.replace('.html/', `.html/${appLocale}/`);
          }
        }
      } else {
        const paramName = langParamApp.includes(app) ? (app === 'community' ? 'profile.language' : 'lang') : 'locale';

        const reg = new RegExp(`(${paramName}=)(.*?)(?=&|$)`);
        const loc = (url.match(reg) || [])[2];

        if (loc !== undefined) {
          localizedUrl = url.replace(reg, '$1' + appLocale);
        } else {
          localizedUrl = url + (url.includes('?') ? '&' : '?') + paramName + '=' + appLocale; 
        }
      }
    }

    return localizedUrl;
  } catch (e) {
    if (process.env.NODE_ENV === 'development') {
      console.error(`[getLocalizedURL] Error: ${e.message}`);
    }
  }

  return url;
};

export default getLocalizedURL;



