import React from 'react';
import {
  injectIntl,
} from 'react-intl';
import SpectrumProvider from '@react/react-spectrum/Provider';
import {
  LIGHT_MODE,
  THEME_IDENTIFIER,
  LOCALE_ZH_HANT,
  LOCALE_ZH_TW,
} from 'src/constants';
import { ThemeProvider } from 'styled-components';
import {
  string,
  node,
  shape,
} from 'prop-types';
import { IntlPropType } from '@astock/core-react-i18n-utils';

/**
 * Web Platform abstraction over [React Spectrum's <CoreSpectrumProvider>](https://react-spectrum.corp.adobe.com/components/Provider)
 * @param {Object} props
 * @returns {React.Component}
 */
function CoreSpectrumProvider({
  intl,
  children,
  theme,
  ...props
}) {
  let {
    locale,
  } = intl;

  /**
   * React-Spectrum does not support zh-Hant.
   * @see https://git.corp.adobe.com/React/react-spectrum-v2/tree/master/src/Pagination/intl, for example.
   * Therefore we need to map zh-Hant to something else, otherwise React-Spectrum components will not be localized.
   */
  if (locale === LOCALE_ZH_HANT) {
    locale = LOCALE_ZH_TW;
  }

  return (
    <SpectrumProvider
      locale={locale}
      theme={theme}
      {...props}
    >
      <ThemeProvider theme={{ [THEME_IDENTIFIER]: theme }}>
        {children}
      </ThemeProvider>
    </SpectrumProvider>
  );
}

CoreSpectrumProvider.propTypes = {
  intl: shape(IntlPropType).isRequired,
  theme: string,
  children: node.isRequired,
};

CoreSpectrumProvider.defaultProps = {
  theme: LIGHT_MODE,
};

// TODO use hooks with react-intl >= 3.0
export default injectIntl(CoreSpectrumProvider);
