import React from 'react';
import LogoIcon from 'src/shared/LogoIcon';
import LogoWithText from 'src/shared/LogoWithText';
import TransparentLogoWithText from 'src/shared/TransparentLogoWithText';
import {
  stockLogoDefaultProps,
  stockLogoPropTypes,
} from 'src/propTypes';
import { useConfig } from '@astock/core-react-context/config';

// style
import {
  StyledLogo,
  StyledLogoLink,
  StyledLogoContainer,
} from 'src/stockLogo/stockLogo.style';

/**
 * Logo component
 * @param {Object} props
 * @returns {ReactElement}
 */
function Logo({
  height,
  hideText,
  textColorDark,
  width,
  onClick,
  link,
  isClickable,
}) {
  // Accommodate adaptive links and their translations if needed
  const title = typeof link.title === 'object' ? link.title.defaultMessage : link.title;
  const {
    shouldUseTransparentLogo,
    componentStyle: {
      hero: {
        contextLogoWidth,
      } = {},
    } = {},
  } = useConfig();

  const customWidth = contextLogoWidth || width;
  const CustomLogoWithText = shouldUseTransparentLogo ? TransparentLogoWithText : LogoWithText;
  const LogoComponent = () => (
    <StyledLogo>
      {
         hideText
           ? <LogoIcon height={height} />
           : <CustomLogoWithText width={customWidth} textColorDark={textColorDark} />
       }
    </StyledLogo>
  );

  if (!isClickable) {
    return (
      <StyledLogoContainer
        data-t="stock-logo-container"
      >
        <LogoComponent />
      </StyledLogoContainer>
    );
  }
  return (
    <StyledLogoLink
      data-t="stock-logo-link"
      onClick={onClick}
      title={title}
      aria-label={link.title}
      {...link}
    >
      <LogoComponent />
    </StyledLogoLink>
  );
}

/**
 * Stock logo, 3 options available:
 * 1. (default) Stock logo + Adobe Stock text
 * 2. Stock Logo only
 * @param {Object} props
 * @returns {ReactElement}
 */
function StockLogo({
  height,
  hideText,
  textColorDark,
  width,
  onClick,
  link,
  isClickable,
}) {
  return (
    Logo({
      height,
      hideText,
      textColorDark,
      width,
      onClick,
      link,
      isClickable,
    })
  );
}

StockLogo.defaultProps = stockLogoDefaultProps;
Logo.defaultProps = stockLogoDefaultProps;

StockLogo.propTypes = stockLogoPropTypes;
Logo.propTypes = stockLogoPropTypes;

export default StockLogo;
