import getAppLocale from './getAppLocale';
import { createLocaleFromIMS, normalize } from './utils';

/*
 * @param {string} app - app code
 * @param {string} locale - locale name
 * @param {string} imsCountryCode - two-character IMS country code
 * @returns {{locale: string, match: boolean}}
 */
const getAppLocaleFromIMS = (app, locale, imsCountryCode) => {
  if (typeof app === 'string') {
    app = app.replace(/-/g, '_');
  }

  let { locale: newLocale, match } = createLocaleFromIMS(locale, imsCountryCode);
  let appLocale = getAppLocale(app, newLocale);

  // we have to support mini_plans a different list (a few less) than adobe.com
  // https://jira.corp.adobe.com/browse/LS-7436
  if (app === 'mini_plans' || app === 'mini_plans_photoshop_lightroom_start_trial') {
    match = match && (newLocale === normalize(appLocale, app));
  }

  return {
    match,
    locale: appLocale
  };
}

export default getAppLocaleFromIMS;



