import {
  string,
  shape,
  func,
  arrayOf,
  element,
  bool,
  object,
  symbol,
  oneOfType,
} from 'prop-types';

/**
 * @desc properties on the IntlPropType were derived by console logging or (inspecting the Intl object)
 */
const IntlPropType = {
  formats: object,
  messages: object,
  timeZone: string,
  defaultLocale: string,
  defaultFormats: object,
  fallbackOnEmptyString: bool,
  onError: func,
  onWarn: func,
  textComponent: oneOfType([string, symbol]),
  locale: string,
  defaultRichTextElements: arrayOf(element),
  formatters: shape({
    getDateTimeFormat: func,
    getNumberFormat: func,
    getMessageFormat: func,
    getRelativeTimeFormat: func,
    getPluralRules: func,
    getListFormat: func,
    getDisplayNames: func,
  }),
  formatNumber: func,
  formatNumberToParts: func,
  formatRelativeTime: func,
  formatDate: func,
  formatDateToParts: func,
  formatTime: func,
  formatDateTimeRange: func,
  formatTimeToParts: func,
  formatPlural: func,
  formatMessage: func,
  formatList: func,
  formatListToParts: func,
  formatDisplayName: func,
};


export default IntlPropType;
