import React, { createContext, useContext } from 'react';

export const CoreContext = createContext({});
export const CoreContextConsumer = CoreContext.Consumer;

// no need to export this; provider is abstracted and exported below
const CoreProvider = CoreContext.Provider;

/**
 * Supported context namespaces.
 */
export const CONFIG_NAMESPACE = 'config';
export const FEATURE_FLAGS_NAMESPACE = 'featureFlags';
const allowedNameSpaces = [CONFIG_NAMESPACE, FEATURE_FLAGS_NAMESPACE];

/**
 * Returns a namespaced context object populated with data in proper namespaces for the given prop
 * values.
 *
 * This function makes it so that there's a true "API" for this package, and that users cannot just
 * set arbitrary values on context and read them elsewhere. The belief is that would be error prone
 * and also very susceptible to backwards incompatible breaking changes.
 *
 * @param {Object} props
 * @returns {Object} like { config: { foo: 'bar' }}
 */
function creatContextObject(props) {
  const context = {};
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const existingContexts = useContext(CoreContext);

  // merge new Contexts from props with the existing Contexts
  allowedNameSpaces.forEach((nameSpace) => {
    context[nameSpace] = {
      ...existingContexts[nameSpace],
      ...props[nameSpace],
    };
  });

  return context;
}

/**
 * Set up core context usage.
 *
 * @param {Object} props
 * @returns {React.Component}
 */
export function CoreContextProvider(props) {
  return (
    <CoreProvider
      value={creatContextObject(props)}
      {...props}
    />
  );
}

/**
 * React hook to access core contextual data, optionally filtered down to a specific namespace.
 *
 * @param {String?} namespace data namespace to return
 * @returns {*} whatever is stored in context
 */
export function useCoreContext(namespace) {
  const coreContextData = useContext(CoreContext);

  if (namespace) {
    return coreContextData[namespace];
  }

  return coreContextData;
}
