import * as locales from './locales';
import { findKey, getAllLocales, lang, script, region, getRegionDefaultLocale, getUmbrellaRegions, isUmbrellaRegionApp, findValueByKeyIgnoreCase } from './utils';

/*
 * @param {string} app - app code. For a complete apps code, see https://git.corp.adobe.com/intl/adobe-locales#supported-apps
 * @param {string} locale - locale name, it does not have to be the one destination app uses.
 * @param {string} fallback - fallback locale name, it does not have to be the one destination app uses.
 * @returns {string} destination app locale name. Returns "" if passed locale is not supported in the app. 
 */
const getAppLocale = (app, locale, fallback) => {
  // must be a valid app
  if (locales[app] === undefined) {
    console.warn(`[@intl/adobe-locales] App "${app}" is not supported. Please refer to https://git.corp.adobe.com/intl/adobe-locales#supported-apps`);
    return '';
  }

  // test locale
  if (typeof locale === 'string' && locale.substring(0, 2) === 'zz') {
    const en = getAppLocale(app, 'en-US');

    return en.includes('-') ? 
            'zz-ZZ' : en.includes('_') ? 
            'zz_ZZ' : 
            'zz'
  }
  
  let appLocale;

  // exact locale
  if (locale && locales[app][locale] !== undefined) {
    appLocale = locale;
  } else {

    // otherwise find a match in all locales
    const adobeLocales = getAllLocales();

    if (locale && !(lang(locale) === '' && adobeLocales[locale] === undefined)) {
      let normalizedLocale = findValueByKeyIgnoreCase(adobeLocales, locale);

      if (normalizedLocale === undefined) {
        if (locale.length === 2) { // region code or lang code only
          normalizedLocale = getRegionDefaultLocale(locale) || locale
        } else {
          normalizedLocale = locale.includes('_') ? locale.replace(/_/g, '-') : locale
        }
      }

      appLocale = findKey(locales[app], (v) => v === normalizedLocale);

      // try umbrella regions
      if (appLocale === undefined && isUmbrellaRegionApp(app)) {
        const umbrellaRegion = getUmbrellaRegions(region(locale), app);

        if (umbrellaRegion) {
          // try lang and region combination first
          const regionLocaleX = `${lang(locale)}-x-${umbrellaRegion.toUpperCase()}`;
          const regionLocale = locale.slice(0, -2) + umbrellaRegion.toUpperCase();

          appLocale = findKey(locales[app], (v) => v === regionLocaleX || v === regionLocale) 
          // try region only
          if (appLocale === undefined && locales[app][umbrellaRegion] !== undefined) {
            appLocale = umbrellaRegion;
          }
        }
      }

      // try lang and script combination
      if (appLocale === undefined && script(normalizedLocale) !== '') {
        const withScript = normalizedLocale.substr(0, 7).toLowerCase();

        appLocale = findKey(locales[app], (v) => v.substr(0, 7).toLowerCase() === withScript);
      }

      // try lang only
      if (appLocale === undefined) {
        const onlyLang = normalizedLocale.substr(0, 2).toLowerCase();

        appLocale = findKey(locales[app], (v) => v.substr(0, 2).toLowerCase() === onlyLang);
      }

      // finally try region only
      if (appLocale === undefined) {
        const onlyRegion = normalizedLocale.split('-').pop().toLowerCase();

        appLocale = findKey(locales[app], v => v.substr(v.length - onlyRegion.length, onlyRegion.length).toLowerCase() === onlyRegion);
      }
    }

    if (appLocale === undefined && fallback !== undefined) {
      appLocale = findKey(locales[app], (v) => v === fallback || v === adobeLocales[fallback]);
    }
  }

  // stock has nothing for en-US locale
  if (app === 'stock' && appLocale === 'us') {
    appLocale = '';
  }
  
  return appLocale || '';
}

export default getAppLocale;



