import styled from 'styled-components';
import DropdownButton from '@react/react-spectrum/DropdownButton';
import { MenuItem } from '@react/react-spectrum/Menu';
import AdobeLogo from '@react/react-spectrum/Icon/AdobeLogo';
import Label from '@react/react-spectrum/Label';
import {
  SPECTRUM_COLOR_GRAY_300,
  SPECTRUM_COLOR_GRAY_700,
  SPECTRUM_COLOR_GRAY_800,
  SPECTRUM_COLOR_GRAY_900,
  SPECTRUM_COLOR_STATIC_WHITE,
  SPECTRUM_TYPOGRAPHY_FONT_SIZE_100,
} from 'src/design';
import { fontSmoothing } from 'src/design/stock/helpers';
import {
  NAVBAR_BREAKPOINTS,
  NAVBAR_BREAKPOINT_3X,
  NAVBAR_BREAKPOINT_2X,
  NAVBAR_BREAKPOINT_XLARGE,
  NAVBAR_BREAKPOINT_LARGE,
  NAVBAR_BREAKPOINT_MEDIUM,
  NAVBAR_BREAKPOINT_XSMALL,
} from './breakpoints';

// New navbar height
const NAVBAR_HEIGHT = 56;

// Stock Logo Width
const STOCK_LOGO_WIDTH = 108;

// Widths for Media queries
// 1600px
const XXXLARGE_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_3X]}px`;
// 1440px
const XXLARGE_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_2X]}px`;
// 1280px
const XLARGE_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_XLARGE]}px`;
// 1439px
const JUST_BELOW_XXLARGE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_2X] - 1}px`;
// 1024px
const LARGE_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_LARGE]}px`;
// 1023px
const JUST_BELOW_LARGE_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_LARGE] - 1}px`;
// 1279px
const JUST_BELOW_XLARGE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_XLARGE] - 1}px`;
// 961px
const JUST_ABOVE_MEDIUM_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_MEDIUM] + 1}px`;
// 480px
const XS_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_XSMALL]}px`;
// 481px
const JUST_ABOVE_XS_DEVICE_BREAKPOINT = `${NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_XSMALL] + 1}px`;

const StyledBar = styled.div`
  height: ${NAVBAR_HEIGHT}px;
  width: 100%;
  display: inline-flex;
  background: ${SPECTRUM_COLOR_STATIC_WHITE};
  align-items: center;
  border-bottom: 2px solid ${SPECTRUM_COLOR_GRAY_300};
  position: relative;
  z-index: 3;
`;

const StyledNavBar = styled.div`
  height: inherit;
  display: inline-flex;
`;

const StyledLeftContainer = styled.div`
  display: inline-flex;

  .left-nav-toggle {
    margin: 0;
  }
`;

const StyledStockLogoContainer = styled(StyledLeftContainer)`
  svg {
    margin-top: 2px;
    margin-left: 20px;
  }
`;

const StyledNavLink = styled.a`
  ${fontSmoothing()};
  margin-left: 24px;
  margin-top: 2px;
  text-decoration: none;
  color: ${({ selected }) => (selected ? SPECTRUM_COLOR_GRAY_900 : SPECTRUM_COLOR_GRAY_700)};
  line-height: 4.1;
  border-bottom: ${({ selected }) => (selected ? `2px solid ${SPECTRUM_COLOR_GRAY_900}` : '2px solid transparent')};
  border-radius: 1px;
  height: ${NAVBAR_HEIGHT - 2}px;
  font-size: ${SPECTRUM_TYPOGRAPHY_FONT_SIZE_100}px;

  &:hover,
  &:active,
  &:focus {
    color: ${SPECTRUM_COLOR_GRAY_900};

    .navlink--icon {
      visibility: visible;
    }
  }

  &:last-child {
    margin-right: 20px;
  }
`;

const StyledNavLinkIconContainer = styled.div`
  display: inline-flex;
  position: absolute;
  margin-left: 4px;
  margin-top: 15px;
  visibility: hidden;
`;

const StyledDropdownLink = styled.a`
  text-decoration: none;
  color: ${SPECTRUM_COLOR_GRAY_800};
`;

const StyledRightNavContainer = styled.div`
  display: inline-flex;
  position: absolute;
  right: 0;
  margin-top: 2px;
`;

const StyledRightNavLink = styled.a`
  ${fontSmoothing()};
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  margin-right: 24px;
  color: ${SPECTRUM_COLOR_GRAY_700};
  transition: color 130ms ease-out;
  line-height: 46px;
  font-size: ${SPECTRUM_TYPOGRAPHY_FONT_SIZE_100}px;
  &:hover {
    color: ${SPECTRUM_COLOR_GRAY_900};
  }
`;

const StyledSignInLink = styled(StyledRightNavLink)`
  margin-right: 20px;

  @media only screen and (max-width: ${XS_DEVICE_BREAKPOINT}) {
    margin-right: 0;
  }
`;

const StyledMoreContainer = styled.div`
  display: inline-flex;

  &&& {
    padding: 0;
    top: 2px;
 }
`;

const StyledFixedCenterSpace = styled.div`
  width: 60px;
  height: inherit;
`;

const NavDropdownButton = styled(DropdownButton)`
  &&& {
      border: none;
      color: ${SPECTRUM_COLOR_GRAY_700};
      transition: color 130ms ease-out;
      &:hover,
      &:active,
      &.is-selected,
      &.is-selected:hover
      {
        background-color: transparent;
        box-shadow: none;
        color: ${SPECTRUM_COLOR_GRAY_900};
        border: none;
       }
    }
`;

const StyledMoreDropdownButton = styled(NavDropdownButton)`
  &&& {
     padding: 0;
     top: 1px;
  }

  .spectrum-Icon {
    position: absolute;
    right: -10px;
    margin-top: 2px;
    pointer-events: auto;
  }

  .spectrum-ActionButton-label {
    padding-left: 0;
  }
`;

const StyledMoreMenuItem = styled(MenuItem)`
  &&& {
    ${fontSmoothing()};
    min-width: 176px;
  }
`;

const StyledHelpDropdownContainer = styled.div`
  height: 46px;
`;

const StyledHelpDropdownButton = styled(NavDropdownButton)`
  &&& {
    top: 7px;
  }

  // hide below 480px (xs device)
  @media only screen and (max-width: ${XS_DEVICE_BREAKPOINT}) {
    display: none;
  }
`;

const StyledAdobeLogoIcon = styled(AdobeLogo)`
  margin-left: 20px;
`;

const StyledAvatarIconContainer = styled.div`
  align-self: center;
  margin-left: 10px;
  margin-right: 14px;
  cursor: pointer;
`;

/** Browser-width/Device-specific styles */

/**
 * A wrapper that only displays above the 3x (1600px) breakpoint.
 */
const ShowOn3XDevices = styled.div`
  display: none;

  @media only screen and (min-width: ${XXXLARGE_DEVICE_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays above the 2x (1440px) breakpoint.
 */
const ShowOn2XDevices = styled.div`
  display: none;

  @media only screen and (min-width: ${XXLARGE_DEVICE_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays between 1280-1439px breakpoint.
 */
const ShowOnXLargeDevices = styled.div`
  display: none;

  @media only screen and (min-width: ${XLARGE_DEVICE_BREAKPOINT}) and (max-width: ${JUST_BELOW_XXLARGE_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays above 1024px breakpoint.
 */
const ShowAboveLargeDevices = styled.div`
 display: none;

 @media only screen and (min-width: ${LARGE_DEVICE_BREAKPOINT}) {
   display: contents;
 }
`;

/**
 * A wrapper that only displays between 1024-1279px breakpoint.
 */
const ShowOnLargeDevices = styled.div`
  display: none;

  @media only screen and (min-width: ${LARGE_DEVICE_BREAKPOINT}) and (max-width: ${JUST_BELOW_XLARGE_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays between 961-1023px breakpoint.
 */
const ShowOnMediumDevices = styled.div`
  display: none;

  @media only screen and (min-width: ${JUST_ABOVE_MEDIUM_BREAKPOINT}) and (max-width: ${JUST_BELOW_LARGE_DEVICE_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays above the xs + 1 (481px) breakpoint.
 */
const ShowAboveXSmallBreakpoint = styled.div`
   display: none;

   @media only screen and (min-width: ${JUST_ABOVE_XS_DEVICE_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays above the medium + 1 (961px) breakpoint.
 */
const ShowAboveMediumBreakpoint = styled.div`
   display: none;

   @media only screen and (min-width: ${JUST_ABOVE_MEDIUM_BREAKPOINT}) {
    display: contents;
  }
`;

/**
 * A wrapper that only displays below the xs (480px) breakpoint.
 */
const ShowOnXSDevice = styled.div`
  display: none;

  @media only screen and (max-width: ${XS_DEVICE_BREAKPOINT}) {
    display: contents;
  }
`;

const StyledLabel = styled(Label)`
  &&& {
    background-color: ${SPECTRUM_COLOR_GRAY_300};
    color: ${SPECTRUM_COLOR_GRAY_900};
    align-self: center;
    margin-right: 15px;
    height: 20px;

    span {
      line-height: 1.6;
    }
  }
`;

export {
  STOCK_LOGO_WIDTH,
  ShowOn3XDevices,
  ShowOn2XDevices,
  ShowOnXLargeDevices,
  ShowAboveLargeDevices,
  ShowOnLargeDevices,
  ShowOnMediumDevices,
  ShowAboveXSmallBreakpoint,
  ShowAboveMediumBreakpoint,
  ShowOnXSDevice,
  StyledBar,
  StyledNavBar,
  StyledLeftContainer,
  StyledStockLogoContainer,
  StyledNavLink,
  StyledNavLinkIconContainer,
  StyledDropdownLink,
  StyledRightNavContainer,
  StyledRightNavLink,
  StyledSignInLink,
  StyledMoreContainer,
  StyledFixedCenterSpace,
  StyledMoreDropdownButton,
  StyledMoreMenuItem,
  StyledHelpDropdownContainer,
  StyledHelpDropdownButton,
  StyledAdobeLogoIcon,
  StyledAvatarIconContainer,
  StyledLabel,
};
