import { string } from 'prop-types';
import styled, { css } from 'styled-components';

export const StyledAvatar = styled.img`
  border-radius: 4px;
  height: ${({ dimension }) => dimension};
  width: ${({ dimension }) => dimension};

  ${({ rounded }) => rounded && css`
    border-radius: 50%;
  `}
`;

StyledAvatar.propTypes = {
  dimension: string,
};
