import {
  bool,
  oneOf,
  string,
} from 'prop-types';
import React from 'react';
import {
  AVATAR_SIZES,
  AVATAR_INITIAL,
  AVATAR_XSMALL,
  AVATAR_SMALL,
  AVATAR_MEDIUM_LARGE,
  AVATAR_LARGE,
} from 'src/constants/avatar';
import {
  StyledAvatar,
} from './avatar.style';

/**
 * Get the dimensions for Avatar given the size
 * @param {string} size
 * @returns {string} the dimensions in px
 */
export const getAvatarDimension = (size) => {
  if (AVATAR_SIZES && Object.prototype.hasOwnProperty.call(AVATAR_SIZES, size)) {
    return `${AVATAR_SIZES[size]}px`;
  }

  return `${AVATAR_SIZES[AVATAR_INITIAL]}px`;
};

/**
 * Avatar component.
 *
 * @returns {ReactElement}
 */
function Avatar({
  alt,
  imageUrl,
  dataT,
  rounded,
  size,
}) {
  const dimension = getAvatarDimension(size);

  return (
    <StyledAvatar
      alt={alt}
      data-t={dataT}
      dimension={dimension}
      rounded={rounded}
      src={imageUrl}
    />
  );
}

Avatar.defaultProps = {
  rounded: false,
  size: AVATAR_INITIAL,
};

Avatar.propTypes = {
  alt: string.isRequired,
  imageUrl: string.isRequired,
  dataT: string.isRequired,
  rounded: bool,
  size: oneOf([AVATAR_INITIAL, AVATAR_XSMALL, AVATAR_SMALL, AVATAR_MEDIUM_LARGE, AVATAR_LARGE]),
};

export default Avatar;
