import {
  arrayOf,
  bool,
  func,
  element,
  shape,
  string,
} from 'prop-types';
import React, {
  Fragment,
  isValidElement,
} from 'react';

import { MenuDivider, MenuHeading } from '@react/react-spectrum/Menu';
import Help from '@react/react-spectrum/Icon/Help';
import ChevronDown from '@react/react-spectrum/Icon/ChevronDown';
import {
  LANGUAGE_EN_US,
} from '@astock/stock-locales/languageTags';
import StockLogo from 'src/stockLogo';

import {
  STOCK_LOGO_WIDTH,
  ShowOn3XDevices,
  ShowOn2XDevices,
  ShowOnXLargeDevices,
  ShowAboveLargeDevices,
  ShowOnLargeDevices,
  ShowOnMediumDevices,
  ShowAboveXSmallBreakpoint,
  ShowAboveMediumBreakpoint,
  ShowOnXSDevice,
  StyledBar,
  StyledNavBar,
  StyledLeftContainer,
  StyledStockLogoContainer,
  StyledRightNavContainer,
  StyledRightNavLink,
  StyledSignInLink,
  StyledMoreContainer,
  StyledFixedCenterSpace,
  StyledMoreDropdownButton,
  StyledHelpDropdownContainer,
  StyledHelpDropdownButton,
  StyledAdobeLogoIcon,
  StyledAvatarIconContainer,
  StyledLabel,
} from './navbarMarketplace.style';
import {
  getSlug,
  getBreakpointXXLargeLinks,
  getBreakpointXLargeLinks,
  getBreakpointMediumLinks,
  getBreakpointLargeLinks,
  getDropdownLinkComponents,
  getLeftNavLinkComponents,
  isUserSignedOut,
} from './helpers';
import {
  NAVBAR_BREAKPOINTS,
  NAVBAR_BREAKPOINT_2X,
  NAVBAR_BREAKPOINT_XLARGE,
  NAVBAR_BREAKPOINT_LARGE,
  NAVBAR_BREAKPOINT_MEDIUM,
} from './breakpoints';

/**
 * NavbarMarketplace component.
 *
 * @returns {ReactElement}
 */
function NavbarMarketplace({
  accountOrSignIn,
  leftLinks,
  rightLinks,
  removalPriorityLinks,
  helpDropdownLinks,
  stockLogoLink,
  onStockLogoLinkClick,
  onAdobeLogoIconClick,
  adobeLogoLink,
  moreNavLinkObject,
  locale,
  isGloballySafeCollectionEnabled,
  defaultEntitlementQuota,
  WaffleLeftNav,
  LeftLinksV2,
}) {
  const xxLargeNavLinks = getBreakpointXXLargeLinks({
    leftLinks, removalPriorityLinks, locale, isGloballySafeCollectionEnabled,
  });
  const xLargeNavLinks = getBreakpointXLargeLinks({
    leftLinks, removalPriorityLinks, locale, isGloballySafeCollectionEnabled,
  });
  const largeNavLinks = getBreakpointLargeLinks({
    leftLinks, removalPriorityLinks, locale, isGloballySafeCollectionEnabled,
  });
  const mediumNavLinks = getBreakpointMediumLinks({
    leftLinks, removalPriorityLinks, locale, isGloballySafeCollectionEnabled,
  });

  const showSignInLink = isUserSignedOut(accountOrSignIn);

  /**
   * Credit Labels:
   * For all non-EN_US languages:
   * If no. of badges >= 2, hide all of them for all browser widths
   * If no. of badges === 1, show >= 1600px
   *
   * For EN_US, if no. of badges <= 2, show above 1440px
   *
   * @return {*}
   */
  const getCreditLabelsComponent = () => {
    const creditLabelElements = defaultEntitlementQuota && defaultEntitlementQuota.map((creditLabel, index) => (
      <StyledLabel
        key={`quota-key-${index + 1}`}
        data-t={`quota-by-license-badge-${index + 1}`}
      >
        <span data-t={`quota-by-license-badge-text-${index + 1}`}>{creditLabel}</span>
      </StyledLabel>
    ));

    if ((locale !== LANGUAGE_EN_US && creditLabelElements.length > 1) || creditLabelElements.length > 2) {
      return null;
    }

    return (locale === LANGUAGE_EN_US) ? (
      <ShowOn2XDevices>
        {creditLabelElements}
      </ShowOn2XDevices>
    ) : (
      <ShowOn3XDevices>
        { creditLabelElements }
      </ShowOn3XDevices>
    );
  };

  /**
   * Helper to build the Right Links Component depending on device width
   * Sell and Pricing:
   * Special condition for RU and RU-like languages
   * Hide Sell, Pricing links below 1280px
   * For all other languages, hide below 1024px
   *
   * @return {*}
   */
  const getRightLinksComponent = () => {
    const rightLinkElements = rightLinks.map((rightLink) => {
      const slug = getSlug(rightLink.dataTPrefix);
      return (
        <StyledRightNavLink
          key={`${slug}-${rightLink.href}`}
          data-t={`navbar-${slug}-button`}
          href={rightLink.href}
          title={rightLink.text}
          onClick={rightLink.onClick}
        >
          <span data-t={`navbar-${slug}-button-text`}>{rightLink.text}</span>
        </StyledRightNavLink>
      );
    });

    const creditLabelsComponent = !showSignInLink && getCreditLabelsComponent();

    /**
     * Sell and Pricing aka "rightLinkElements" need to render >=961px (ShowAboveMediumBreakpoint)
     */
    return (
      <ShowAboveMediumBreakpoint>
        { creditLabelsComponent }
        { rightLinkElements }
      </ShowAboveMediumBreakpoint>
    );
  };

  /**
   * Helper to get Left Nav Component which includes:
   * 1. Left Nav Links
   * 2. More Dropdown Button and Links
   *
   * @param {Array} navLinks
   * @param {Number} breakpoint
   * @returns {*} leftNavComponent
   */
  const getLeftNavComponent = (navLinks, breakpoint) => {
    // find current active asset link to highlight in the navbar
    const selectedAssetLinkIndex = navLinks.leftNavLinks.findIndex(navLink => navLink.active);
    const leftLinksNavComponent = (
      <StyledNavBar
        aria-label="navbar-asset-links"
        selectedIndex={selectedAssetLinkIndex}
      >
        {getLeftNavLinkComponents(navLinks.leftNavLinks, breakpoint)}
      </StyledNavBar>
    );

    const moreDropdownNavComponent = navLinks.moreDropdownLinks && navLinks.moreDropdownLinks.length ? (
      <StyledMoreContainer>
        <StyledMoreDropdownButton
          label={moreNavLinkObject.text}
          icon={<ChevronDown size="XS" />}
          data-t={`navbar-${moreNavLinkObject.dataTPrefix}`}
          closeOnSelect={false}
          onClick={moreNavLinkObject.onClick}
          title={moreNavLinkObject.text}
        >
          { getDropdownLinkComponents(navLinks.moreDropdownLinks, 'navbar-more-dropdown-menu-item') }
        </StyledMoreDropdownButton>
      </StyledMoreContainer>
    ) : null;

    return (
      <Fragment>
        {leftLinksNavComponent}
        {moreDropdownNavComponent}
      </Fragment>
    );
  };

  /**
   * Helper method that returns left nav components for different breakpoints
   * @returns {*} leftNav components for different breakpoints
   */
  const getLeftNavBreakpointComponents = () => (
    <>
      {/* 1440px and above */}
      <ShowOn2XDevices>
        { getLeftNavComponent(xxLargeNavLinks, NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_2X]) }
      </ShowOn2XDevices>

      {/* 1280-1439px */}
      <ShowOnXLargeDevices>
        { getLeftNavComponent(xLargeNavLinks, NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_XLARGE]) }
      </ShowOnXLargeDevices>

      {/* 1024-1279px */}
      <ShowOnLargeDevices>
        { getLeftNavComponent(largeNavLinks, NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_LARGE]) }
      </ShowOnLargeDevices>

      {/* 961-1023px */}
      <ShowOnMediumDevices>
        { getLeftNavComponent(mediumNavLinks, NAVBAR_BREAKPOINTS[NAVBAR_BREAKPOINT_MEDIUM] + 1) }
      </ShowOnMediumDevices>
    </>
  );

  return (
    <StyledBar data-t="navbar">

      {/* Logo and Waffle button container */}
      <StyledLeftContainer>
        {WaffleLeftNav}
        <ShowAboveXSmallBreakpoint>
          <StyledStockLogoContainer>
            <StockLogo
              width={STOCK_LOGO_WIDTH}
              textColorDark
              link={stockLogoLink}
              onClick={onStockLogoLinkClick}
            />
          </StyledStockLogoContainer>
        </ShowAboveXSmallBreakpoint>
        <StyledStockLogoContainer>
          <ShowOnXSDevice>
            <StockLogo
              hideText
              textColorDark
              link={stockLogoLink}
              onClick={onStockLogoLinkClick}
            />
          </ShowOnXSDevice>
        </StyledStockLogoContainer>
      </StyledLeftContainer>

      {/* Left Nav Links V2 or More Dropdown */}
      {
        LeftLinksV2 ? (
          <ShowAboveLargeDevices>
            {LeftLinksV2}
          </ShowAboveLargeDevices>
        ) : getLeftNavBreakpointComponents()
      }

      <StyledFixedCenterSpace />

      {/* Right Links Container - Sell, Pricing, Sign-in/Profile, Help Dropdown, Adobe Logo */}
      <StyledRightNavContainer>
        {/* Show Sell, Pricing only >= 961px */}
        { getRightLinksComponent() }

        {/* Sign-in link */}
        {
          showSignInLink && (
            <StyledSignInLink
              data-t="navbar-sign-in-button"
              href={accountOrSignIn.href}
              title={accountOrSignIn.title}
              {...accountOrSignIn}
            >
              <span data-t="navbar-sign-in-button-text">
                {accountOrSignIn.text}
              </span>
            </StyledSignInLink>
          )
        }

        {/* Show Help Dropdown only >= 481px */}
        <ShowAboveXSmallBreakpoint>
          {
            helpDropdownLinks && (
              <StyledHelpDropdownContainer>
                <StyledHelpDropdownButton
                  alignRight
                  icon={<Help size="S" />}
                  data-t="navbar-help-icon"
                  aria-label={helpDropdownLinks.heading.text}
                >
                  <MenuHeading>{helpDropdownLinks.heading.text}</MenuHeading>
                  <MenuDivider />
                  { getDropdownLinkComponents(helpDropdownLinks.links, 'navbar-help-dropdown-menu-item') }
                </StyledHelpDropdownButton>
              </StyledHelpDropdownContainer>
            )
          }
        </ShowAboveXSmallBreakpoint>
        {/* Avatar and Profile Dropdown */}
        {
          !showSignInLink && (
            <StyledAvatarIconContainer>
              { accountOrSignIn }
            </StyledAvatarIconContainer>
          )
        }
        {/* Show Adobe Logo if user is signed-out */}
        {
          showSignInLink && (
            <StyledRightNavLink
              href={adobeLogoLink.href}
              title={adobeLogoLink.title}
              data-t="navbar-adobe-logo-link"
              onClick={onAdobeLogoIconClick}
            >
              <StyledAdobeLogoIcon size="S" data-t="navbar-adobe-logo-icon" />
            </StyledRightNavLink>
          )
        }
      </StyledRightNavContainer>
    </StyledBar>
  );
}

NavbarMarketplace.defaultProps = {
  accountOrSignIn: null,
  stockLogoLink: {
    href: 'https://stock.adobe.com',
    title: "Return to Adobe Stock's homepage",
  },
  adobeLogoLink: {
    href: 'https://adobe.com',
    title: "Return to Adobe's homepage",
  },
  onStockLogoLinkClick: null,
  onAdobeLogoIconClick: null,
  defaultEntitlementQuota: [],
  LeftLinksV2: null,
};

NavbarMarketplace.propTypes = {
  /** Should either be an account dropdown component or an object with the necessary data to render a sign-in link */
  // eslint-disable-next-line consistent-return
  accountOrSignIn: ({ accountOrSignIn }) => {
    if (accountOrSignIn && !(isValidElement(accountOrSignIn) || (accountOrSignIn.href && accountOrSignIn.text && accountOrSignIn.title))) {
      return new Error('Expected accountOrSignIn to either be a valid React element or an object with an href, text, and title');
    }
  },
  stockLogoLink: shape({
    href: string.isRequired,
    title: string.isRequired,
  }),
  adobeLogoLink: shape({
    href: string.isRequired,
    title: string.isRequired,
  }),
  /** Array of links to show on the left side of the desktop view ("Images", "Videos"...)
   * `active` should be set to `true` for the current page.
   */
  leftLinks: arrayOf(shape({
    active: bool,
    text: string,
    dataTPrefix: string.isRequired,
    title: string.isRequired,
    href: string.isRequired,
    showLinkOutIcon: bool,
  })).isRequired,
  removalPriorityLinks: arrayOf(shape({
    active: bool,
    text: string,
    dataTPrefix: string.isRequired,
    title: string.isRequired,
    href: string.isRequired,
  })).isRequired,
  /** Array of links to show on the right side of the desktop view ("Sell", "Pricing"...)
   */
  rightLinks: arrayOf(shape({
    href: string,
    text: string,
    dataTPrefix: string.isRequired,
    title: string,
  })).isRequired,
  onStockLogoLinkClick: func,
  onAdobeLogoIconClick: func,
  helpDropdownLinks: shape({
    heading: shape({
      title: string,
      text: string.isRequired,
      dataTPrefix: string.isRequired,
      openInNewTab: bool,
    }).isRequired,
    links: arrayOf(shape({
      number: string,
      text: string.isRequired,
      title: string,
      dataTPrefix: string.isRequired,
      href: string.isRequired,
      onClick: func,
    })),
  }).isRequired,
  locale: string.isRequired,
  moreNavLinkObject: shape({
    text: string.isRequired,
    dataTPrefix: string.isRequired,
    onClick: func.isRequired,
  }).isRequired,
  isGloballySafeCollectionEnabled: bool.isRequired,
  defaultEntitlementQuota: arrayOf(shape(string.isRequired)),
  WaffleLeftNav: element.isRequired,
  LeftLinksV2: element,
};

export default NavbarMarketplace;
