import * as locales from './locales';
import regions from './locales/_region';
import apps, { umbrellaRegionApp, umbrellaRegions } from './apps';
import getAppLocale from './getAppLocale';

const pattern = /^([a-zA-Z]{2})(?:[_-]+([a-zA-Z]{3})(?=$|[_-]+))?(?:[_-]+([a-zA-Z]{4})(?=$|[_-]+))?(?:[_-]+([a-zA-Z]{2}|[0-9]{3})(?=$|[_-]+))?(?:-x-([a-zA-Z0-9]{1,8})(?=$))?$/;

const _match    = locale => locale.match(pattern) || [];

/**
 * given an object, return its value based on passed condition
 * @param {object} obj
 * @param {object} fun
 */
const findKey = (obj, fun) => Object.keys(obj).find(x => fun(obj[x]));

/**
* given an object, return its value based on passed case-insensitive key
* @param {object} obj
* @param {string} key
*/
const findValueByKeyIgnoreCase = (obj, key) => key !== undefined ? obj[Object.keys(obj).filter(k => k.toLowerCase() === key.toLowerCase())[0]] : undefined;

/* 
  https://www.w3.org/International/articles/language-tags/#rfc

  pattern: language-extlang-script-region-variant-extension-privateuse

  type      pattern
  language  2-letter or 3-letter
  extlang   3-letter
  script    4-letter
  region    2-letter or 3-number
  private   -x-****
*/
/**
 * "language" sub-tab of a locale
 * @param {string} locale - locale name
 */
const lang      = locale => _match(locale)[1] || '';

/**
 * "extlang" sub-tab of a locale
 * @param {string} locale - locale name
 */
const extlang   = locale => _match(locale)[2] || '';

/**
 * "script" sub-tab of a locale
 * @param {string} locale - locale name
 */
const script    = locale => _match(locale)[3] || '';

/**
 * "region" sub-tab of a locale
 * @param {string} locale - locale name
 */
const region    = locale => _match(locale)[4] || '';

/**
 * "private use" sub-tab of a locale
 * @param {string} locale - locale name
 */
const priuse    = locale => _match(locale)[5] || '';

/**
 * "language" + "script" sub-tab of a locale
 * @param {string} locale - locale name
 * @param {string} camelizeScriptName - if camelize Script Name
 */
const langScript = (locale, camelizeScriptName = false) => {
  let ls = '';
  const matches = _match(locale);

  if (matches.length > 0) {
    if (matches[3] !== undefined) {  // has script
      if (camelizeScriptName === true) {
        ls = `${matches[1]}-${matches[3].charAt(0).toUpperCase()}${matches[3].slice(1)}`;
      } else {
        ls = `${matches[1]}-${matches[3]}`;
      }
    } else if (matches[1].toLowerCase() === 'zh' && matches[4] !== undefined) {

      let region = matches[4].toLowerCase();

      if (['tw', 'hk', 'mo'].includes(region)) {
        ls = 'zh-Hant';
      } else {
        ls = 'zh-Hans';
      }
    } else {
      ls = matches[1];
    }
  }

  return ls;
}

/*
  Generate all app locale mapping.
*/
const allLocales = {};

Object.keys(locales).forEach(app => {
  const appLocales = locales[app];

  Object.keys(appLocales).forEach(appLocale => {
    const mappedLocale = appLocales[appLocale];
    // const lowerMappedLocale = mappedLocale.toLowerCase();

    allLocales[appLocale] = mappedLocale;

    if (allLocales[mappedLocale] === undefined) {
      allLocales[mappedLocale] = mappedLocale;
    }

    // if (allLocales[lowerMappedLocale] === undefined) {
    //   allLocales[lowerMappedLocale] = mappedLocale;
    // }
  });
});

/**
 * get all app locale mapping
 */
const getAllLocales = () => allLocales;

/**
 * get all app locale mapping
 */
const getUmbrellaRegions = (region, app) => {
  return findKey(umbrellaRegions, (v) => {
    if (Array.isArray(v)) {
      return v.includes(region.toLowerCase())
    } 
  
    return v[app === 'adobedotcom' ? 'adobedotcom' : 'generic'].includes(region.toLowerCase())
    
  });
};

/**
 * check if one of apps that have Umbrella Regions such as "mena", "sea"
 */
const isUmbrellaRegionApp = (app) => umbrellaRegionApp.includes(app);

/**
 * returns BCP-47 form of a locale
 * @param {string} locale - locale name
 * @param {string} app - optional, which can handle app-specific case, like "uk" returns "en-GB" for app "adobedotcom", while it returns "uk-UA" in other cases.
 */
const normalize = (locale, app) => {
  let normalizedLocale;

  if (app) {
    const appLocale = getAppLocale(app, locale);

    normalizedLocale = locales[app][appLocale];
  } 
  
  return normalizedLocale || findValueByKeyIgnoreCase(getAllLocales(), locale);
}

/**
 * helps to construct a locale using the language of an app and the user's IMS country. 
 * If the language is not supported by adobe.com for that country, it will change to the 
 * default language of that country that adobe.com supports. For example, if en-US is the 
 * app language and JP is the IMS code, this will result in "ja-JP"
 */
const createLocaleFromIMS = (locale, imsCountryCode) => {
  let match = false;
  let newLocale = 'en-US';
  
  if (typeof locale === 'string' && typeof imsCountryCode === 'string' && imsCountryCode.length === 2) {
    let language = langScript(locale, true);
    let imsCountryCodeU = imsCountryCode.toUpperCase();
    let countryLangs = regions[imsCountryCodeU];

    if (countryLangs !== undefined) {
      if (countryLangs.includes(language)) {
        match = true;
      } else {
        language = countryLangs[0] || 'en';
      }

      newLocale = `${language}-${imsCountryCodeU}`;
    }    
  }

  return {
    match,
    locale: newLocale
  };
}

/**
 * helper to strip url protocol and www
 * @param {string} url - url name
 * @returns {string} url w/o protocol and www
 */
const stripUrl = (url) => url.replace(/^(https?:\/\/)/, '').replace(/^(www\.)/, '');

/**
 * get app code name from a url
 * @param {string} url - url name
 * @returns {string} app name
 */
const getAppNameFromURL = (url) => {

  if (typeof url === 'string' && url.length) {
    const strippedUrl = stripUrl(url.trim().toLowerCase());

    for (const [name, urls] of apps) {
      const productionURLs = Array.isArray(urls.production) ? urls.production : [urls.production]
      const stageURLs = Array.isArray(urls.stage) ? urls.stage : [urls.stage]

      for (const appURL of [...productionURLs, ...stageURLs]) {
        if (strippedUrl.indexOf(stripUrl(appURL)) === 0) {
          return name
        }
      }
    }
  }

  return undefined
}

/**
 * 
 * @param {string} region - 2 char country code
 * @returns default locale
 */
const getRegionDefaultLocale = (region) => {
  let defaultLocale
  
  if (typeof region === 'string' && region.length === 2) {
    const regionCode = region.toUpperCase()

    if (regions[regionCode] && regions[regionCode][0]) {
      const lang = regions[regionCode][0]

      defaultLocale = `${lang}-${regionCode}`
    }
  }

  return defaultLocale
}

/**
 * get country supported languages list
 * @param {string} imsCountryCode - country code 2-char
 * @returns {array} languages list. default to ['en']
 */
const getCountryLanguages = (imsCountryCode) => {
  const defaultLangs = ['en'];
  const langs = (typeof imsCountryCode === 'string') ? (regions[imsCountryCode.toUpperCase()] || []) : defaultLangs;

  return langs.length ? langs : defaultLangs;  
}

export { findKey, 
         findValueByKeyIgnoreCase,
         lang, 
         extlang, 
         script, 
         region, 
         regions,
         priuse, 
         normalize, 
         langScript, 
         getAllLocales, 
         getUmbrellaRegions, 
         isUmbrellaRegionApp, 
         getAppNameFromURL, 
         createLocaleFromIMS, 
         getCountryLanguages,
         getRegionDefaultLocale };




