/**
 * All languages supported by Adobe Stock.
 * Format: ISO 639-1
 *
 * @type {String}
 * @see https://en.wikipedia.org/wiki/List_of_ISO_639-1_codes
 */
export const LANGUAGE_CS = 'cs';  // Czech
export const LANGUAGE_DA = 'da';  // Danish
export const LANGUAGE_DE = 'de';  // German
export const LANGUAGE_EN = 'en';  // English
export const LANGUAGE_ES = 'es';  // Spanish
export const LANGUAGE_FI = 'fi';  // Finnish
export const LANGUAGE_FR = 'fr';  // French
export const LANGUAGE_HU = 'hu';  // Hungarian
export const LANGUAGE_ID = 'id';  // Indonesian
export const LANGUAGE_IT = 'it';  // Italian
export const LANGUAGE_JA = 'ja';  // Japanese
export const LANGUAGE_KO = 'ko';  // Korean
export const LANGUAGE_MS = 'ms';  // Malay
export const LANGUAGE_NB = 'nb';  // Norwegian
export const LANGUAGE_NL = 'nl';  // Dutch
export const LANGUAGE_PL = 'pl';  // Polish
export const LANGUAGE_PT = 'pt';  // Portuguese
export const LANGUAGE_RU = 'ru';  // Russian
export const LANGUAGE_SV = 'sv';  // Swedish
export const LANGUAGE_TH = 'th';  // Thai
export const LANGUAGE_TR = 'tr';  // Turkish
export const LANGUAGE_UK = 'uk';  // Ukrainian
export const LANGUAGE_ZH = 'zh';  // Chinese
export const LANGUAGE_ZZ = 'zz';  // Test

/**
 * All languages supported by Adobe Stock.
 * Format: BCP 47
 *
 * @type {String}
 * @see https://tools.ietf.org/html/bcp47
 * @see https://www.w3.org/International/questions/qa-choosing-language-tags
 * @see https://www.w3.org/International/articles/language-tags/
 */
export const LANGUAGE_CS_CZ = 'cs-CZ';            // Czech
export const LANGUAGE_DA_DK = 'da-DK';            // Danish
export const LANGUAGE_DE_DE = 'de-DE';            // German (Germany)
export const LANGUAGE_EN_US = 'en-US';            // English (United States of America)
export const LANGUAGE_ES_ES = 'es-ES';            // Spanish (Spain)
export const LANGUAGE_FI_FI = 'fi-FI';            // Finnish (Finland)
export const LANGUAGE_FR_FR = 'fr-FR';            // French (France)
export const LANGUAGE_HU_HU = 'hu-HU';            // Hungarian
export const LANGUAGE_ID_ID = 'id-ID';            // Indonesian
export const LANGUAGE_IT_IT = 'it-IT';            // Italian (Italy)
export const LANGUAGE_JA_JP = 'ja-JP';            // Japanese
export const LANGUAGE_KO_KR = 'ko-KR';            // Korean
export const LANGUAGE_MS_MY = 'ms-MY';            // Malay
export const LANGUAGE_PT_BR = 'pt-BR';            // Portuguese (Brazil)
export const LANGUAGE_PT_PT = 'pt-PT';            // Portuguese (Portugal)
export const LANGUAGE_NB_NO = 'nb-NO';            // Norwegian Bokml
export const LANGUAGE_NL_NL = 'nl-NL';            // Dutch
export const LANGUAGE_PL_PL = 'pl-PL';            // Polish
export const LANGUAGE_RU_RU = 'ru-RU';            // Russian
export const LANGUAGE_SV_SE = 'sv-SE';            // Swedish
export const LANGUAGE_TH_TH = 'th-TH';            // Thai
export const LANGUAGE_TR_TR = 'tr-TR';            // Turkish
export const LANGUAGE_UK_UA = 'uk-UA';            // Ukrainian
export const LANGUAGE_ZH_HANS = 'zh-Hans';        // Chinese (Simplified)
export const LANGUAGE_ZH_HANT = 'zh-Hant';        // Chinese (Traditional)
export const LANGUAGE_ZZ_ZZ = 'zz-ZZ';            // Test

/**
 * Stock default language.
 *
 * @type {String}
 */
export const STOCK_LANGUAGE_DEFAULT = LANGUAGE_EN_US;

/**
 * Stock locales mapping for react-intl v2.
 * @see https://unpkg.com/browse/react-intl@2.9.0/locale-data/
 * @description Some language specifics are simplified to main languages (e.g. 'pt-BR' => 'pt').
 *
 * addLocaleData is removed in react-intl v3.
 * @see https://github.com/formatjs/react-intl/blob/5a67172a8d0573da45d1667db188cfa05694bcca/docs/Upgrade-Guide.md#breaking-api-changes
 *
 * @type {String}
 */
export const LOCALE_TO_REACT_INTL = {
  [LANGUAGE_CS_CZ]: LANGUAGE_CS,
  [LANGUAGE_DA_DK]: LANGUAGE_DA,
  [LANGUAGE_DE_DE]: LANGUAGE_DE,
  [LANGUAGE_EN_US]: LANGUAGE_EN,
  [LANGUAGE_ES_ES]: LANGUAGE_ES,
  [LANGUAGE_FI_FI]: LANGUAGE_FI,
  [LANGUAGE_FR_FR]: LANGUAGE_FR,
  [LANGUAGE_HU_HU]: LANGUAGE_HU,
  [LANGUAGE_ID_ID]: LANGUAGE_ID,
  [LANGUAGE_IT_IT]: LANGUAGE_IT,
  [LANGUAGE_JA_JP]: LANGUAGE_JA,
  [LANGUAGE_KO_KR]: LANGUAGE_KO,
  [LANGUAGE_MS_MY]: LANGUAGE_MS,
  [LANGUAGE_NB_NO]: LANGUAGE_NB,
  [LANGUAGE_NL_NL]: LANGUAGE_NL,
  [LANGUAGE_PL_PL]: LANGUAGE_PL,
  [LANGUAGE_PT_BR]: LANGUAGE_PT,
  [LANGUAGE_PT_PT]: LANGUAGE_PT,
  [LANGUAGE_RU_RU]: LANGUAGE_RU,
  [LANGUAGE_SV_SE]: LANGUAGE_SV,
  [LANGUAGE_TH_TH]: LANGUAGE_TH,
  [LANGUAGE_TR_TR]: LANGUAGE_TR,
  [LANGUAGE_UK_UA]: LANGUAGE_UK,
  [LANGUAGE_ZH_HANS]: LANGUAGE_ZH,
  [LANGUAGE_ZH_HANT]: LANGUAGE_ZH,
  // there is no locale data for zz-ZZ, and so we use english
  [LANGUAGE_ZZ_ZZ]: LANGUAGE_EN,
};
