import getAppLocale from './getAppLocale';
import { getAppNameFromURL } from './utils';
import { langParamApp } from './apps';

/*
 * @param {string} url - app url
 * @returns {string} locale name the app uses. Returns '' if no valid locale found
 */
const parseLocaleParam = (url) => {
  
  let locale;

  if (typeof url === 'string') {
    const app = getAppNameFromURL(url);

    if (app) {
      // Adobe Account uses 'lang=xx' parameter
      const paramName = langParamApp.includes(app) ? 'lang' : 'locale';
      const regex = new RegExp(`(${paramName}=)(.*?)(?=&|$)`);

      locale = (url.match(regex) || [])[2] || '';
      locale = getAppLocale(app, locale, 'en-US');
    }
  }
  
  return locale;
}

export default parseLocaleParam;



