import React from 'react';
import {
  injectIntl,
} from 'react-intl';
import {
  objectOf,
  object,
  string,
  shape,
} from 'prop-types';
import { Provider as SpectrumV3Provider } from '@react-spectrum/provider';
import { theme as defaultTheme } from '@react-spectrum/theme-default';
import { IntlPropType } from '@astock/core-react-i18n-utils';
import {
  LOCALE_ZH_HANT,
  LOCALE_ZH_TW,
} from 'src/constants';

/**
 * Web Platform abstraction over the [React Spectrum V3 Provider](https://react-spectrum.adobe.com/react-spectrum/Provider.html)
 *
 * @param {Object} intl
 * @param {Object} props
 * @returns {React.Component}
 */
function CoreSpectrumV3Provider({ intl, ...props }) {
  const {
    colorScheme,
    v3Theme,
    v3Margin,
  } = props;

  /**
   * React Spectrum V3 does not support zh-Hant.
   * @see https://github.com/adobe/react-spectrum/tree/main/packages/%40react-spectrum/pagination/intl, for example.
   * Therefore we need to map zh-Hant to something else, otherwise React-Spectrum components will not be localized.
   */
  let {
    locale,
  } = intl;

  if (locale === LOCALE_ZH_HANT) {
    locale = LOCALE_ZH_TW;
  }

  return (
    <SpectrumV3Provider
      colorScheme={colorScheme}
      locale={locale}
      theme={v3Theme}
      margin={v3Margin}
      {...props}
    />
  );
}

CoreSpectrumV3Provider.defaultProps = {
  colorScheme: 'light',
  v3Theme: defaultTheme,
  v3Margin: 'size-0',
};

CoreSpectrumV3Provider.propTypes = {
  colorScheme: string,
  intl: shape(IntlPropType).isRequired,
  v3Theme: objectOf(object),
  v3Margin: string,
};

export default injectIntl(CoreSpectrumV3Provider);
