import {
  bool,
  func,
  number,
  shape,
  string,
} from 'prop-types';

export const stockLogoDefaultProps = {
  /* size of the logo in the navbar as default height */
  height: 28,
  /* size of the logo in stock-web and used for rebranded logo with text */
  width: 170,
  textColorDark: false,
  hideText: false,
  isClickable: true,
  link: {
    href: '',
    title: '',
  },
  onClick: null,
};

export const stockLogoPropTypes = {
  height: number,
  width: number,
  textColorDark: bool,
  hideText: bool,
  isClickable: bool,
  link: shape({
    href: string.isRequired,
    title: string.isRequired,
  }),
  onClick: func,
};
