import getLocalizedURL from './getLocalizedURL';
import getAppLocaleFromIMS from './getAppLocaleFromIMS';
import { getAppNameFromURL } from './utils';

/*
 * @param {string} url - the destination app url
 * @param {string} locale - locale you want to pass, it doesn not have to be the one destination app uses.
 * @param {string} imsCountryCode - two-character IMS country code
 * @returns {{url: string, match: boolean}}
 */
const getLocalizedURLFromIMS = (url, locale, imsCountryCode) => {
  const app = getAppNameFromURL(url);

  let { locale: newLocale, match } = getAppLocaleFromIMS(app, locale, imsCountryCode);

  return {
    match,
    url: getLocalizedURL(url, newLocale)
  };
}

export default getLocalizedURLFromIMS;



