/* eslint valid-jsdoc: off */

import styled from 'styled-components';
import { STOCK_BREAKPOINT_XSMALL } from 'src/design/stock/breakpoints';
import { media } from 'src/design/stock/helpers';

/**
 * Style of the main container.
 *
 * Note:
 * Flex is used to align the SVGs while removing the space underneath that usually comes along
 * with inline elements.
 * The second SVG (CSS selector `svg + svg`) is the text of the logo:
 * - The text is hidden on small screens.
 * - `block` is used because the SVGs are flex items.
 */
const StyledLogo = styled.div`
  align-items: center;
  display: flex;

  svg + svg {
    display: none;
  }

  ${media[STOCK_BREAKPOINT_XSMALL]`
    svg + svg {
      display: block;
    }
  `}
`;

const StyledLogoLink = styled.a`
  display: inline-flex;
  cursor: pointer;
`;

const StyledLogoContainer = styled.div`
  display: inline-flex;
`;

export {
  StyledLogo,
  StyledLogoLink,
  StyledLogoContainer,
};
