import React, {
  isValidElement,
} from 'react';
import LinkOut from '@react/react-spectrum/Icon/LinkOut';
import {
  LANGUAGE_CS_CZ,
  LANGUAGE_FR_FR,
  LANGUAGE_ES_ES,
  LANGUAGE_DA_DK,
  LANGUAGE_DE_DE,
  LANGUAGE_HU_HU,
  LANGUAGE_JA_JP,
  LANGUAGE_RU_RU,
  LANGUAGE_NB_NO,
  LANGUAGE_NL_NL,
  LANGUAGE_PL_PL,
  LANGUAGE_PT_BR,
  LANGUAGE_PT_PT,
  LANGUAGE_SV_SE,
  LANGUAGE_TR_TR,
  LANGUAGE_UK_UA,
} from '@astock/stock-locales/languageTags';
import {
  STOCK_BREAKPOINT_MEDIUM_LEGACY,
  STOCK_BREAKPOINT_LARGE,
  STOCK_BREAKPOINT_XLARGE,
  STOCK_BREAKPOINT_XXLARGE,
} from 'src/design/stock/breakpoints';
import {
  StyledDropdownLink,
  StyledMoreMenuItem,
  StyledNavLink,
  StyledNavLinkIconContainer,
} from './navbarMarketplace.style';

/**
 * We have 3 language-specific breakpoints with the default catch-all language as EN
 * Russian and Russian-like languages have the largest breakpoints i.e. max no of links hidden
 * Japanese is slightly shorter and hence has fewer links hidden for each breakpoint
 * French and French-like languages fall under the third category
 * The catch-all language is English with the fewest links hidden for each breakpoint
 *
 * @type {Map<any, any>}
 */
export const LANGUAGE_TO_BREAKPOINT_CATEGORY = new Map([
  // Breakpoint 1 - Largest
  [LANGUAGE_RU_RU, LANGUAGE_RU_RU],
  [LANGUAGE_CS_CZ, LANGUAGE_RU_RU],
  [LANGUAGE_UK_UA, LANGUAGE_RU_RU],

  // Breakpoint 2
  [LANGUAGE_JA_JP, LANGUAGE_JA_JP],
  [LANGUAGE_HU_HU, LANGUAGE_JA_JP],
  [LANGUAGE_TR_TR, LANGUAGE_JA_JP],

  // Breakpoint 3
  [LANGUAGE_FR_FR, LANGUAGE_FR_FR],
  [LANGUAGE_ES_ES, LANGUAGE_FR_FR],
  [LANGUAGE_DA_DK, LANGUAGE_FR_FR],
  [LANGUAGE_DE_DE, LANGUAGE_FR_FR],
  [LANGUAGE_NL_NL, LANGUAGE_FR_FR],
  [LANGUAGE_NB_NO, LANGUAGE_FR_FR],
  [LANGUAGE_PL_PL, LANGUAGE_FR_FR],
  [LANGUAGE_PT_BR, LANGUAGE_FR_FR],
  [LANGUAGE_PT_PT, LANGUAGE_FR_FR],
  [LANGUAGE_SV_SE, LANGUAGE_FR_FR],
]);

/**
 * Helper function to get the "slug" for data-t parameters
 * @param {string} element
 * @return {string}
 */
export const getSlug = element => (element ? element.toLowerCase().replace(/\s+/g, '-') : '');

/**
 * Filter-out more-dropdown links from left nav links
 * @param {Array} leftLinks
 * @param {Array} moreDropdownLinks
 * @return {Array} reducedLeftNavLinks
 */
export const getReducedLeftNavLinks = ({
  leftLinks,
  moreDropdownLinks,
}) => (
  leftLinks.filter(
    (nav) => {
      const index = moreDropdownLinks.findIndex(elem => elem.href === nav.href);
      return index === -1;
    }
  )
);

/**
 * Returns slice size for 'More' dropdown
 * @param {String} locale
 * @param {String} screenSize
 * @return {Number} slice size
 */
const getDropdownSliceSize = (locale, screenSize) => {
  const sliceObj = {
    [LANGUAGE_JA_JP]: {
      [STOCK_BREAKPOINT_XXLARGE]: -1,
      [STOCK_BREAKPOINT_XLARGE]: -3,
      [STOCK_BREAKPOINT_LARGE]: -6,
      [STOCK_BREAKPOINT_MEDIUM_LEGACY]: -7,
    },
    [LANGUAGE_RU_RU]: {
      [STOCK_BREAKPOINT_XXLARGE]: -3,
      [STOCK_BREAKPOINT_XLARGE]: -5,
      [STOCK_BREAKPOINT_LARGE]: -7,
      [STOCK_BREAKPOINT_MEDIUM_LEGACY]: -7,
    },
    [LANGUAGE_FR_FR]: {
      [STOCK_BREAKPOINT_XLARGE]: -2,
      [STOCK_BREAKPOINT_LARGE]: -5,
      [STOCK_BREAKPOINT_MEDIUM_LEGACY]: -6,
    },
    default: {
      [STOCK_BREAKPOINT_LARGE]: -4,
      [STOCK_BREAKPOINT_MEDIUM_LEGACY]: -4,
    },
  };

  return sliceObj[locale][screenSize];
};

/**
 * Get Nav Links for 1440px and above
 * @param {Array} leftLinks
 * @param {Array} removalPriorityLinks
 * @param {String} locale
 * @param {Boolean} isGloballySafeCollectionEnabled
 * @return {{moreDropdownLinks: Array, leftNavLinks: Array}}
 */
export const getBreakpointXXLargeLinks = ({
  leftLinks,
  removalPriorityLinks,
  locale,
  isGloballySafeCollectionEnabled,
}) => {
  let leftNavLinks = [...leftLinks];
  let moreDropdownLinks = [];
  const mappedLanguage = LANGUAGE_TO_BREAKPOINT_CATEGORY.get(locale);

  if (!isGloballySafeCollectionEnabled) {
    switch (mappedLanguage) {
      case LANGUAGE_JA_JP:
      case LANGUAGE_RU_RU:
        moreDropdownLinks = [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_XXLARGE));
        break;
      default: break;
    }

    leftNavLinks = getReducedLeftNavLinks({ leftLinks, moreDropdownLinks });
  }

  return {
    leftNavLinks,
    moreDropdownLinks,
  };
};

/**
 * Get Nav Links for 1280-1439px
 * @param {Array} leftLinks
 * @param {Array} removalPriorityLinks
 * @param {String} locale
 * @param {Boolean} isGloballySafeCollectionEnabled
 * @return {{moreDropdownLinks: Array, leftNavLinks: Array}}
 */
export const getBreakpointXLargeLinks = ({
  leftLinks,
  removalPriorityLinks,
  locale,
  isGloballySafeCollectionEnabled,
}) => {
  let leftNavLinks = [...leftLinks];
  let moreDropdownLinks = [];
  const mappedLanguage = LANGUAGE_TO_BREAKPOINT_CATEGORY.get(locale);

  if (!isGloballySafeCollectionEnabled) {
    switch (mappedLanguage) {
      case LANGUAGE_JA_JP:
      case LANGUAGE_RU_RU:
      case LANGUAGE_FR_FR:
        moreDropdownLinks = [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_XLARGE));
        break;
      default: break;
    }

    leftNavLinks = getReducedLeftNavLinks({ leftLinks, moreDropdownLinks });
  }

  return {
    leftNavLinks,
    moreDropdownLinks,
  };
};

/**
 * Get Nav Links for 1024-1279px
 * @param {Array} leftLinks
 * @param {Array} removalPriorityLinks
 * @param {String} locale
 * @param {Boolean} isGloballySafeCollectionEnabled
 * @return {{moreDropdownLinks: Array, leftNavLinks: Array}}
 */
export const getBreakpointLargeLinks = ({
  leftLinks,
  removalPriorityLinks,
  locale,
  isGloballySafeCollectionEnabled,
}) => {
  let moreDropdownLinks = [];
  const mappedLanguage = LANGUAGE_TO_BREAKPOINT_CATEGORY.get(locale);

  switch (mappedLanguage) {
    case LANGUAGE_JA_JP:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? [...removalPriorityLinks].slice(-2) : [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_LARGE));
      break;
    case LANGUAGE_RU_RU:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? [...removalPriorityLinks].slice(-4) : [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_LARGE));
      break;
    case LANGUAGE_FR_FR:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? moreDropdownLinks : [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_LARGE));
      break;
    default:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? moreDropdownLinks : [...removalPriorityLinks].slice(getDropdownSliceSize('default', STOCK_BREAKPOINT_LARGE));
      break;
  }

  const leftNavLinks = getReducedLeftNavLinks({ leftLinks, moreDropdownLinks });

  return {
    leftNavLinks,
    moreDropdownLinks,
  };
};

/**
 * Get Nav Links for 961-1024px
 * @param {Array} leftLinks
 * @param {Array} removalPriorityLinks
 * @param {String} locale
 * @param {Boolean} isGloballySafeCollectionEnabled
 * @return {{moreDropdownLinks: Array, leftNavLinks: Array}}
 */
export const getBreakpointMediumLinks = ({
  leftLinks,
  removalPriorityLinks,
  locale,
  isGloballySafeCollectionEnabled,
}) => {
  let moreDropdownLinks = [];
  const mappedLanguage = LANGUAGE_TO_BREAKPOINT_CATEGORY.get(locale);

  switch (mappedLanguage) {
    case LANGUAGE_JA_JP:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? [...removalPriorityLinks].slice(-2) : [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_MEDIUM_LEGACY));
      break;
    case LANGUAGE_RU_RU:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? [...removalPriorityLinks].slice(-4) : [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_MEDIUM_LEGACY));
      break;
    case LANGUAGE_FR_FR:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? moreDropdownLinks : [...removalPriorityLinks].slice(getDropdownSliceSize(mappedLanguage, STOCK_BREAKPOINT_MEDIUM_LEGACY));
      break;
    default:
      moreDropdownLinks = isGloballySafeCollectionEnabled ? moreDropdownLinks : [...removalPriorityLinks].slice(getDropdownSliceSize('default', STOCK_BREAKPOINT_MEDIUM_LEGACY));
      break;
  }

  const leftNavLinks = getReducedLeftNavLinks({ leftLinks, moreDropdownLinks });

  return {
    leftNavLinks,
    moreDropdownLinks,
  };
};

/**
 * Get Left Nav Link Components
 * @param {Array} navLinks
 * @param {Number} breakpoint
 * @return {*}
 */
export const getLeftNavLinkComponents = (navLinks, breakpoint) => (
  navLinks.map((navLink) => {
    const slug = getSlug(navLink.dataTPrefix);
    return (
      <StyledNavLink
        href={navLink.href}
        key={`${slug}-${navLink.href}`}
        title={navLink.title}
        data-t={`navbar-${slug}-lp-button`}
        data-breakpoint={`navbar-${slug}-${breakpoint}`}
        selected={navLink.active}
        onClick={navLink.onClick}
        {...(navLink.showLinkOutIcon && { target: '_blank' })}
      >
        {navLink.text}
        {navLink.showLinkOutIcon && <StyledNavLinkIconContainer className="navlink--icon"><LinkOut size="XS" /></StyledNavLinkIconContainer>}
      </StyledNavLink>
    );
  })
);

/**
 * Get Dropdown Link Components
 * @param {Array} links
 * @param {string} dataTPrefix
 * @return {*}
 */
export const getDropdownLinkComponents = (links, dataTPrefix = 'navbar-dropdown-menu-item') => (
  links.map((linkElem) => {
    const slug = getSlug(linkElem.dataTPrefix);
    const onClickHandler = () => {
      if (linkElem.onClick) {
        linkElem.onClick();
      }

      if (linkElem.openInNewTab) {
        window.open(linkElem.href);
      } else {
        window.location.assign(linkElem.href);
      }
    };

    return (
      <StyledMoreMenuItem
        role="menuitem"
        value={linkElem.text}
        key={`${slug}-${linkElem.href}`}
        data-t={`${dataTPrefix}-${slug}`}
        onClick={onClickHandler}
      >
        <StyledDropdownLink
          href={linkElem.href}
          title={linkElem.title || linkElem.text}
        >
          {linkElem.text}
        </StyledDropdownLink>
      </StyledMoreMenuItem>
    );
  })
);

/**
 * Check if user is signed-in or not depending on the "accountOrSignIn" prop passed in by StockNavbarMarketplace
 * Return true if the user is signed-out and vice-versa
 * @param {Object} accountOrSignIn
 * @return {boolean}
 */
export const isUserSignedOut = accountOrSignIn => (
  typeof accountOrSignIn === 'object'
  && accountOrSignIn !== null
  && !isValidElement(accountOrSignIn)
  && accountOrSignIn.href
  && accountOrSignIn.text
  && accountOrSignIn.title
);
