import * as locales from './locales';

/*
 * @param {string} app - app code. For a complete apps code, see https://git.corp.adobe.com/intl/adobe-locales#supported-apps
 * @returns {array} app supported locale codes. Returns undefined if passed locale is not supported in the app. 
 */
const getAppLocales = (app) => {
  return locales[app] && Object.keys(locales[app]);
}

export default getAppLocales;



